<template>
  <div class="gutter-horizontal">

    <div class="row">
      <div class="span-10-lg offset-3-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs">

        <h1 class="upcase">Post Survey</h1>
        <p>To help us better serve you and your students please tell us how useful/relevant this program is to you.</p>
        <p>Rate your level of agreement with the following statements on a scale of 1-5. 1 being Strongly Disagree and 5 being Strongly Agree. This training:</p>

      </div>
    </div>

    <response v-for="(caption, index) in captions" v-bind:key="index" v-bind:ind="(index + 1)" v-bind:caption="caption"></response>

  </div>
</template>

<style scoped lang='scss'>

  @import '../styles/variables';

</style>

<script>

  // import pages from './pages'
  import surveyResponse from '../components/organisms/surveyResponse'

  export default {
    data () {
      return {
        captions: ['Increased my level of commitment to change the way I interact with children who are exhibiting the effects of trauma.', 'Made me feel better prepared to work with students who may have been exposed to violence and trauma.', 'Increased my level of commitment to change the way I interact with children who are exhibiting the effects of trauma.', 'Inspires me to work at my school/district to promote a more compassionate and safer school environment for students, parents, teachers, and staff.', 'Has content that is relevant to my work.', 'Made me interested in a more indepth training on this topic.']
      }
    },
    components: {
      // ...pages
      'response': surveyResponse
    },
  }
</script>


