<template>
  <div>
    <!-- title -->
    <div class="row">
      <div class="span-1-lg offset-2-lg span-1-md hide-sm">
        <div class="p subheader survey-number">{{ind}}.</div>
      </div>

      <div class="span-10-lg span-14-md span-14-sm offset-1-sm span-14-xs offset-1-xs ">
        <p class="large">{{caption}}</p>
      </div>
    </div>

    <!-- options -->
    <div class="row survey-options-container center-flex-mobile">
      <div v-for="(option, index) in options" v-bind:key="index" class="span-2-lg span-3-md span-3-sm span-3-xs relative survey-option-outer ratio-survey-card" v-bind:class="getFirstOption(index)">
        <div class="survey-option-inner text-center">
          <h1>{{index + 1}}</h1> 
          <p class="hide-sm">{{option}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped lang='scss'>

  @import '../../styles/variables';

  .survey-options-container {
    margin-bottom: 96px;
  }

  .survey-number {
    position: relative;
    text-align: right;
    top: 50%;
    transform: translateY(-50%);
    padding-right: 24px;
  }

  .survey-option-inner {
    position: relative;
    box-sizing: border-box;
    background: white;
    border: 1px solid #DCDDDF;
    width: 100%;
    height: 100%;
    transition: all $transition-1;
    cursor: pointer;
    p, h1 {
      transition: all $transition-1;
    }
    &:hover, &.hovered {
      border: 1px solid white;
      background: $blue;
      p, h1 {
        color: white;
      }
    }
    @media screen and (max-width: $sm-max) {
      display: flex;
      h1 {
        position: relative;
        margin: auto;
      }
    }
  }

  .survey-option-outer {
    padding-right: 24px;
    @media screen and (max-width: $md-max) {
      padding-right: 0px;
    }
  }

</style>

<script>

  // import pages from './pages'

  export default {
    props: ['ind', 'caption'],
    data () {
      return {
        options: ['Strongly Disagree', 'Somewhat Disagree', 'Neutral or No Opinion', 'Somewhat Agree', 'Strongly Agree'],
        // todo: props
        // ind: 1,
        // caption: 'Increased my level of commitment to change the way I interact with children who are exhibiting the effects of trauma.'
      }
    },
    methods: {
      getFirstOption(index) {
        if (index < 1) {
          return 'offset-3-lg offset-1-md'
        } else {
          return ''
        }
      }
    },
    // components: {
    //   ...pages
    // },
  }
</script>



